<script>
import { mapState } from "vuex";
const sigUtil = require("eth-sig-util");
import { fetchGet } from "@/common/utils/request.js";
import { fetchUpdateProfile } from "@/contactLogic/manage";

export default {
  data() {
    return {
      profileData: {},
    };
  },
  computed: {
    ...mapState(["web3", "ethersprovider", "ethChainID", "ethAddress"]),
    routerlist(){
      return [{to:'/',name:'Home'},{to:'/members',name:'Members'},{name:'profile'}];
    }
  },
  methods: {
    async getProfileList() {
      const profileID = this.$route.params.profileID;
      try {
        const res = await fetchGet(`/api/member/profile/${profileID}`, {});
        console.log(res);
        this.profileData = res.data.data;
      } catch (error) {
        console.log(error);
      }
    },
    openconfirmtDialog() {
      if (this.profileData.address === this.ethAddress) {
        this.$refs.confirm.open(this.profileName);
      } else {
        this.$Notice.error({
          title: "Failed to edit",
          desc: "You cant edit this",
          duration: 10,
        });
      }
    },
    async onChangeName(newName) {
      const from = this.ethAddress;

      const signObject = {
        address: from,
        update_detail: { profile_name: newName },
      };

      const msg = this.web3.utils.toHex(signObject);
      // var msg = '0x1' // hexEncode(text)
      console.log(msg);

      /*  web3.personal.sign not yet implemented!!!
       *  We're going to have to assemble the tx manually!
       *  This is what it would probably look like, though:
        web3.personal.sign(msg, from) function (err, result) {
          if (err) return console.error(err)
          console.log('PERSONAL SIGNED:' + result)
        })
      */

      console.log("CLICKED, SENDING PERSONAL SIGN REQ");
      const params = [msg, from];

      const method = "personal_sign";
      // const _this = this;
      this.web3.currentProvider.sendAsync(
        { method, params, from },
        async (err, result) => {
          if (err) return console.error(err);
          if (result.error) return console.error(result.error);

          console.log("PERSONAL SIGNED:" + JSON.stringify(result.result));

          const signature = result.result;
          const data = await fetchUpdateProfile({
            ...signObject,
            update_detail_signature: signature,
          });

          console.log(data, 3333);
          if (data.code === 200) {
            this.$Notice.success({
              title: "Successfully modified",
              duration: 10,
            });
            this.getProfileList();
          } else {
            this.$Notice.error({
              title: "Failed to edit",
              desc: data.code,
              duration: 10,
            });
          }

          // console.log("recovering...");
          // const msgParams = { data: msg };
          // msgParams.sig = result.result;
          // console.dir({ msgParams });
          // // console.log(sigUtil);
          // const result1 = await _this.web3.eth.personal.ecRecover(
          //   msgParams.data,
          //   msgParams.sig
          // );
          // console.log(result1);
          // const recovered = sigUtil.recoverPersonalSignature(msgParams);
          // console.dir({ recovered });
          //
          // if (recovered === from.toLocaleLowerCase()) {
          //   console.log("SigUtil Successfully verified signer as " + from);
          //   // window.alert("SigUtil Successfully verified signer as " + from);
          // } else {
          //   console.dir(recovered);
          //   console.log(
          //     "SigUtil Failed to verify signer when comparing " +
          //     recovered.result +
          //     " to " +
          //     from
          //   );
          //   console.log("Failed, comparing %s to %s", recovered, from);
          // }
        }
      );
    },
  },
  components: {
    confirmtDialog: () => import("./dialog/confirmDialog.vue"),
  },
  created() {
    this.getProfileList();
  },
};
</script>

